  padding: 1rem 2rem;
  border-radius: 10px;
  background: #0c1626;
  -webkit-transition: all 1s ease-in;
  transition: all 1s ease-in;
  -webkit-animation: cardShuffleIn 1s cubic-bezier(0.075, 0.82, 0.165, 1) both;
          animation: cardShuffleIn 1s cubic-bezier(0.075, 0.82, 0.165, 1) both; }
  .card * {
    -webkit-animation: fadein .5s backwards;
            animation: fadein .5s backwards; }
  .card#SAT {
    -webkit-animation-delay: .2s;
            animation-delay: .2s; }
  .card#SUN {
